<template>
  <div class="apartment-gallery">
    <div class="apartment-gallery__tabs">
      <button
        class="apartment-gallery__tab"
        :class="{ 'apartment-gallery__tab--active': activeTab === 1 }"
        type="button"
        :disabled="activeTab === 1"
        @click="setActiveTab(1)"
      >
        Планировки
      </button>

      <button
        class="apartment-gallery__tab"
        :class="{ 'apartment-gallery__tab--active': activeTab === 2 }"
        type="button"
        :disabled="activeTab === 2"
        @click="setActiveTab(2)"
      >
        Интерьеры
      </button>
    </div>

    <div
      v-if="galleryItems?.length"
      class="apartment-gallery__main-slider-wrap"
    >
      <button class="apartment-gallery__prev" type="button">
        <img
          class="apartment-gallery__prev-icon"
          src="@/assets/icons/arrow-left.svg"
          alt="arrow"
        />
      </button>

      <swiper
        class="apartment-gallery__main-slider"
        :lazy="true"
        :navigation="{
          nextEl: '.apartment-gallery__next',
          prevEl: '.apartment-gallery__prev'
        }"
        :thumbs="{ swiper: thumbsSwiper }"
        :modules="modules"
      >
        <swiper-slide
          v-for="(item, index) in galleryItems"
          class="apartment-gallery__main-slide"
          :key="item + index"
        >
          <img class="apartment-gallery__slide-img" :src="item" />
        </swiper-slide>
      </swiper>

      <button class="apartment-gallery__next" type="button">
        <img
          class="apartment-gallery__next-icon"
          src="@/assets/icons/arrow-left.svg"
          alt="arrow"
        />
      </button>
    </div>

    <swiper
      @swiper="setThumbsSwiper"
      :spaceBetween="10"
      :slidesPerView="2"
      :freeMode="true"
      :watchSlidesProgress="true"
      :modules="modules"
      class="apartment-gallery__nav-slider"
    >
      <swiper-slide
        v-for="(item, index) in galleryItems"
        class="apartment-gallery__nav-slide"
        :key="item + index"
      >
        <img class="apartment-gallery__slide-img" :src="item" />
      </swiper-slide>
    </swiper>
  </div>
</template>

<script setup>
import { FreeMode, Navigation, Thumbs } from "swiper";
import { Swiper, SwiperSlide } from "swiper/vue";

import { ref, computed, defineProps } from "vue";

const props = defineProps({
  layouts: {
    type: Array,
    default: () => []
  },
  interiors: {
    type: Array,
    default: () => []
  }
});

const thumbsSwiper = ref(null);
const activeTab = ref(1);

function setThumbsSwiper(swiper) {
  thumbsSwiper.value = swiper;
}

function setActiveTab(id) {
  activeTab.value = id;
}

//["/images/apartment/a-1.png", "/images/apartment/a-2.jpg"];

const galleryItems = computed(() => {
  return activeTab.value === 1 && props.layouts?.length
    ? props.layouts
    : props.interiors?.length
    ? props.interiors
    : [];
});

const modules = [FreeMode, Navigation, Thumbs];
</script>

<style lang="less">
.apartment-gallery {
  box-sizing: border-box;
  width: 100%;
  padding: 25px 30px 35px;

  background-color: @white;
  box-shadow: 0 5px 13px rgba(186, 189, 191, 0.42);
  border-radius: 20px;

  &__main-slider-wrap {
    display: flex;
    margin-bottom: 35px;
  }

  &__prev,
  &__next {
    display: flex;

    width: 40px;
    height: 40px;
    margin-top: auto;
    margin-bottom: auto;

    background-color: @white;
    border: none;
    border-radius: 50%;
    transition: background-color 0.3s;

    cursor: pointer;

    &:hover {
      @media (hover: hover) {
        background-color: fade(@green, 20%);
      }
    }

    &:active {
      background-color: fade(@green, 20%);
    }
  }

  &__tabs {
    margin-bottom: 15px;
  }

  &__tab {
    height: 30px;
    padding: 0 15px;

    border-radius: 15px;
    border: none;
    background-color: @light-gray2;
    color: @black;
    font-family: @font2;
    font-weight: 500;
    font-size: 15px;
    line-height: 2;

    cursor: pointer;

    &:not(:last-child) {
      margin-right: 18px;
    }

    &--active {
      color: @white;
      background-color: @green;
    }
  }

  &__prev-icon,
  &__next-icon {
    margin: auto;
  }

  &__next-icon {
    transform: rotateZ(180deg);
  }

  &__main-slider {
    width: 100%;
    max-width: 485px;
    height: 380px;

    @media @bw600 {
      height: auto;
    }
  }

  &__main-slide {
    width: 100%;
    height: 100%;
  }

  &__slide-img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &__nav-slider {
    height: 60px;
    width: 130px;
    padding: 3px;

    & .swiper-slide-thumb-active {
      transition: box-shadow 0.2s;
      box-shadow: 0 0 5px 1px @green;
    }
  }

  &__nav-slide {
    box-sizing: border-box;
    height: 100%;
    width: 60px;

    border: 1px solid @light-gray;
  }
}
</style>
