<template>
  <div class="apartment-card">
    <img class="apartment-card__img" :src="img" alt="img" />

    <div class="apartment-card__info">
      <div class="apartment-card__info-top">{{ cost }} ₽</div>

      <button class="apartment-card__info-btn" type="button">
        <img
          class="apartment-card__info-icon"
          src="@/assets/icons/heart-black.svg"
          alt="heart"
        />
      </button>

      <div class="apartment-card__info-middle">
        <div
          v-for="item in info"
          class="apartment-card__middle-item"
          :key="item"
          v-html="item"
        />
      </div>

      <div class="apartment-card__info-bottom">
        {{ company }}
      </div>
    </div>
  </div>
</template>

<script setup>
import { defineProps, computed } from "vue";

const props = defineProps({
  data: {
    type: Object,
    default: () => {}
  }
});

const img = computed(() => {
  return props.data?.img || "";
});

const cost = computed(() => {
  return props.data?.cost || "0";
});

const room = computed(() => {
  return props.data?.room ? props.data?.room + "-комн." : "-";
});

const floor = computed(() => {
  return `${props.data?.floor || "0"}/${props.data?.total_floor || "0"} этаж`;
});

const area = computed(() => {
  return (props.data?.area ? props.data?.area : "0") + `м<sup>2</sup>`;
});

const info = computed(() => {
  return [room.value, floor.value, area.value];
});

const company = computed(() => {
  return props.data?.company || "";
});
</script>

<style lang="less">
.apartment-card {
  display: flex;
  gap: 0 25px;

  padding: 20px;

  font-family: @font2;
  background-color: @white;
  border-radius: 20px;
  box-shadow: 0px 5px 13px rgba(186, 189, 191, 0.42);

  &__img {
    width: 96px;
    height: 96px;

    flex-shrink: 0;
  }

  &__info {
    position: relative;
    flex-grow: 1;
  }

  &__info-top {
    padding-top: 10px;
    margin-bottom: 20px;

    font-weight: 500;
    font-size: 15px;
    line-height: 1;
  }

  &__info-btn {
    position: absolute;
    top: 0;
    right: 0;

    padding: 0;

    background-color: transparent;
    border: none;
  }

  &__info-middle {
    display: flex;
    flex-wrap: wrap;
    gap: 10px 0;

    margin-bottom: 15px;
  }

  &__middle-item {
    width: 50%;

    font-size: 13px;
    font-weight: 400;
    line-height: 1;
  }

  &__info-bottom {
    font-weight: 400;
    font-size: 13px;
    line-height: 1;
    color: @gray2;
  }
}
</style>
