<template>
  <div class="apartment-info-card">
    <header class="apartment-info-card__header">
      <div class="apartment-info-card__price">от {{ price }} ₽</div>

      <div class="apartment-info-card__price-per-month">
        {{ pricePerMonth }} ₽/м2
      </div>
    </header>

    <div class="apartment-info-card__interactive">
      <button class="apartment-info-card__like" type="button">
        <img
          class="apartment-info-card__like-icon"
          src="@/assets/icons/heart.svg"
          alt="heart"
        />

        <span class="apartment-info-card__like-text">В ИЗБРАННОЕ</span>
      </button>

      <div class="apartment-info-card__how-match">
        {{ likes }} человека добавили в избранное
      </div>
    </div>

    <div v-if="info" class="apartment-info-card__table">
      <!--      <template>-->
      <div
        v-for="item in info"
        class="apartment-info-card__table-row"
        :key="item.label"
      >
        <div class="apartment-info-card__table-label" v-html="item.label" />

        <div class="apartment-info-card__table-line" />

        <div class="apartment-info-card__table-value">{{ item.value }}</div>
      </div>
      <!--      </template>-->
    </div>

    <div class="apartment-info-card__info-mortgage">
      <div class="apartment-info-card__mortgage-block">
        <div class="apartment-info-card__mortgage-label">
          Ипотека: от {{ mortgageInterest }}%
        </div>

        <div
          class="apartment-info-card__mortgage-value apartment-info-card__mortgage-value--accent"
        >
          {{ mortgageValue }} ₽/месяц
        </div>
      </div>

      <div class="apartment-info-card__mortgage-block">
        <div class="apartment-info-card__mortgage-label">
          Рассрочка: {{ installmentsInterest }}%
        </div>

        <div class="apartment-info-card__mortgage-value">
          {{ installmentsValue }} ₽/месяц
        </div>
      </div>
    </div>

    <button class="apartment-info-card__btn btn" type="button">
      ДОБАВИТЬ В КОРЗИНУ
    </button>
  </div>
</template>

<script setup>
import { defineProps, computed } from "vue";

const props = defineProps({
  data: {
    type: Object,
    default: () => {}
  }
});

const price = computed(() => props.data?.price || 0);

const pricePerMonth = computed(() => props.data?.price_per_month || 0);

const likes = computed(() => props.data?.likes || 0);

const info = computed(() => props.data?.details || null);

const mortgageInterest = computed(() => props.data?.mortgage?.interest || 0);
const mortgageValue = computed(() => props.data?.mortgage?.value || 0);

const installmentsInterest = computed(
  () => props.data?.installments?.interest || 0
);
const installmentsValue = computed(() => props.data?.installments?.value || 0);
</script>

